html,
body {
  height: 100%;
}

body {
  font-family: "opensans-regular", sans-serif;
  font-weight: 400;
  letter-spacing: 1.05px;
}

.main-wrapper {
  padding: 0;
  min-width: 320px;
  width: 100%;
  position: relative;
  overflow: hidden;
  min-height: 100%;
}

.wrapper {
  min-width: 320px;
  max-width: 100%;
  padding: 0;
  margin: 0 auto;
  position: relative;
}

/* titles */
p {
  font-size: 12px;
  line-height: 16px;
  color: #282828;
  letter-spacing: normal;
  padding: 15px 0 15px 0;
  font-weight: 400;
  letter-spacing: 1.05px;
}

h1 {
  font-size: 24px;
  line-height: 28px;
  color: #2c303d;
  letter-spacing: normal;
  font-family: "oswald-bold", sans-serif;
  padding: 15px 0 15px 0;
  font-weight: 400;
  letter-spacing: 1.05px;
}

h2 {
  font-size: 22px;
  line-height: 26px;
  color: #2c303d;
  letter-spacing: normal;
  font-family: "oswald-bold", sans-serif;
  padding: 15px 0 15px 0;
  font-weight: 400;
  letter-spacing: 1.05px;
}

h3 {
  font-size: 20px;
  line-height: 24px;
  color: #2c303d;
  letter-spacing: normal;
  font-family: "oswald-bold", sans-serif;
  padding: 15px 0 15px 0;
  font-weight: 400;
  letter-spacing: 1.05px;
}

ul,
li {
  list-style: none;
}

img {
  width: 100%;
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

.lined {
  text-decoration: line-through;
}

.header {
  display: block;
}

.footer {
  display: block;
  outline: 1px solid red;
}
